<template>
  <div class="recordWarp">
    <h3>【 购买记录 】</h3>
    <ul class="recordlist">
      <li v-for="item in recordlist" :key="item.coupons" :class="item.status === 2?fontGrey:''">
        <p>卡券编号：{{item.coupon}}
          <mt-button v-show="item.status !== 2" size="small" type="primary" @click.native='handleSubmit'>复制并前往兑换</mt-button>
        </p>
        <p class="resStatus">卡券礼包：{{item.productName}} X{{item.num}}
          <span v-show="item.status === 2">{{item.statusText}}</span>
        </p>
        <p>购买日期：{{ dateForm }}</p>
      </li>

      <!-- <li>
        <p>卡券编号：123333
          <mt-button size="small" type="primary" @click.native='handleSubmit'>复制并前往兑换</mt-button>
        </p>
        <p class="resStatus">卡券礼包：
          <span>statusText</span>
        </p>
        <p>购买日期：</p>
      </li>
      <li :class="font === 1?fontGrey:''">
        <p>卡券编号：
          <mt-button v-show="font === 0" size="small" type="primary" @click.native='handleSubmit'>复制并前往兑换</mt-button>
        </p>
        <p>卡券礼包：</p>
        <p>购买日期：</p>
      </li> -->
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      recordlist: this.$store.state.recordlist,
      fontGrey: 'fontGrey'
    };
  },
  computed: {
    dateForm() {
      let date = this.$store.state.recordlist.orderTime;
      return date ? date.substring(0, 10) : '';
    }
  },
  mounted: function() {
    console.log(this.$store.state.recordlist);
  }
};
</script>

<style>
#app {
  background: none;
}
.recordWarp {
  height: 90%;
  margin: 2% 5%;
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: flex-start;
}
.recordlist {
  height: auto;
  overflow-y: scroll;
}
.recordWarp ul {
  padding: 0 10px;
  margin: 0;
}
.recordWarp li {
  list-style-type: none;
  margin-top: 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid gainsboro;
}
.recordWarp .mint-button--small {
  padding: 0 5px;
  margin-left: 15px;
}
.recordWarp .resStatus {
  display: flex;
  justify-content: space-between;
}
.recordWarp h3 {
  text-align: left;
  margin-bottom: 0;
}
.recordWarp p {
  text-align: left;
  margin: 10px 0;
  font-size: 1em;
}
.fontGrey {
  color: gainsboro;
}
</style>





